/**
 * WP API access data 
 */
const wp_url = `http://union-house.lndo.site`
const api_base = `/wp-json/wp/v2`
const api_resource = '/pages/'

const pages_ids = {
  home: 18,
  about: 20,
  legal: 22,
  register: 24,
  thankYou: 26
}

/**
 * Compose full API url 
 * @param {string|null} suffix 
 */
export const composeApiUrl = ({ page }) => {
  let suffix = (page) ? pages_ids[page] : ''

  return wp_url + api_base + api_resource + suffix
}



