import axios from 'axios'
import { composeApiUrl } from '@/helpers/apiHelper.js'

const initialState = () => ({
  page: null,
  content: null,
  excerpt: null
})

const state = () => (initialState())

const actions = {
  async GET_PAGE({ state, commit }) {
    if ( state.page ) {
      return; 
    }
    
    const response = await axios.get(composeApiUrl({ page: 'home' }))

    if (response.data) {
      commit("SET_PAGE", { page: response.data })
      commit("SET_CONTENT", { content: response.data.content.rendered })
      commit("SET_EXCERPT", { excerpt: response.data.excerpt.rendered })
    }
  }
}

const mutations = {
  SET_PAGE(state, { page }) {
    state.page = page
  },
  SET_CONTENT(state, { content }) {
    state.content = content
  },
  SET_EXCERPT(state, { excerpt }) {
    state.excerpt = excerpt
  }
}

const getters = {
  content: (state) => state.content,
  excerpt: (state) => state.excerpt
}

export default {
  namespaced: true,
  state, actions, mutations, getters
}