<script>
// var aoProtocol = location.protocol;
// if (aoProtocol.indexOf("http") < 0) aoProtocol = "http:";
// var aoCAP = {
//   aid: "40566",
//   fid: "b9903b40-3b69-4aa1-ac76-c7e9988d296b",
//   did: "d-0001",
//   server: "ci58.actonsoftware.com",
//   formName: "mainform",
//   protocol: aoProtocol
// };

/* main imports */
import Vue from "vue";
import { mapGetters } from "vuex";
import VeeValidate from "vee-validate";

/* scripts */
import vSelect from "vue-select";
Vue.component("v-select", vSelect);
Vue.use(VeeValidate);

export default {
  mounted() {},
  data() {
    return {
      optionsHear: [
        "BROKER TOUR",
        "DRIVE OR WALK BY",
        "EMAIL",
        "EVENT",
        "HOME SEARCH PORTAL",
        "MEDIA MENTION",
        "ONLINE AD",
        "REFERRAL: AGENT/BROKER",
        "REFERRAL: HOMEOWNER",
        "REFERRAL: OTHER (FRIEND, FAMILY)",
        "SIGNAGE"
      ],
      optionsType: ["ONE BEDROOM", "TWO BEDROOM", "THREE BEDROOM", "PENTHOUSE"],
      formData: {
        // u: "976e1512a7036a903f56d8483",
        // id: "169b10fc87",
        EMAIL: "",
        FNAME: "",
        LNAME: "",
        ZIP: "",
        MOBILE: "",
        HEAR: "HOW DID YOU HEAR ABOUT US?*",
        TYPE: "DESIRED HOME TYPE",
        BROKER: ""
      },
      formMessage: "",
      canClick: true
    };
  },
  methods: {
    submitForm: function() {
      this.$validator.validateAll().then(result => {
        if (result) {
          this.$refs.circleContainer.classList.add("loading");

          /* test */
          this.responseText = "cc";
          let statusNotFound = "cc";
          let statusSubscribed = "cc";

          /* submit new mail */
          if (this.responseText === statusNotFound) {
            this.formMessage =
              "Success! You have beed added to the list. Redirecting...";

            // fetch(
            //   "https://marketing.unionhousesf.com/acton/eform/40566/b9903b40-3b69-4aa1-ac76-c7e9988d296b/d-ext-0001",
            //   {
            //     method: "POST",
            //     headers: {
            //       "Content-Type": "application/json"
            //     },
            //     body: JSON.stringify({
            //       EMAIL: this.formData.EMAIL,
            //       FNAME: this.formData.FNAME,
            //       LNAME: this.formData.LNAME,
            //       ZIP: this.formData.ZIP,
            //       MOBILE: this.formData.MOBILE,
            //       isAgent:
            //         this.formData.BROKER.toString() === "true" ? true : false,
            //       howDidYouHearAboutUs: this.formData.PLACEHOLDER_HEAR,
            //       TYPE: this.formData.TYPE
            //     })
            //   }
            // );

            setTimeout(() => {
              this.$refs.circleContainer.classList.remove("error");
              this.$refs.circleContainer.classList.add("loaded");
              this.$refs.mainform.submit();
              setTimeout(() => {
                this.$router.push("/thankYou");
              }, 2000);
            }, 3500);

            /* mail already exist */
          } else if (this.responseText === statusSubscribed) {
            this.formMessage =
              "Email is already in database.<br>Click anywhere to return to the form.";
            setTimeout(() => {
              this.$refs.circleContainer.classList.add("error");
            }, 2500);
          }
          return;
        }
      });
    },
    removeErr: function() {
      this.canClick = false;
      if (
        (this.canClick = true) &&
        this.$refs.circleContainer.classList.contains("error")
      ) {
        this.$refs.circleContainer.classList.remove("error");
        this.$refs.circleContainer.classList.remove("loading");
        setTimeout(() => {
          this.canClick = true;
        }, 1000);
      }
    }
  },
  computed: {
    ...mapGetters("register", ["content", "excerpt"])
  },
  async created() {
    await this.$store.dispatch("register/GET_PAGE");
  }
};
</script>

