  <template>
  <div>
    <div class="burger" ref="burger" @click="activeMenu()" :class="burgerStates">
      <span></span>
      <span></span>
      <span></span>
    </div>
    <transition name="fade" mode="out-in">
      <div class="mobileMenu" v-if="active">
        <imageNameLogo class="imageNamelogo"/>
        <nav>
          <router-link to="/About">about</router-link>
          <router-link to="/Register">register</router-link>
          <router-link to="/Legal">legal</router-link>
        </nav>
        <imageBottomDark/>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: false,
      fixed: false
    };
  },
  computed: {
    burgerStates() {
      return {
        active: this.active,
        fixed: this.fixed
      };
    }
  },
  methods: {
    activeMenu: function() {
      this.active === true ? (this.active = false) : (this.active = true);
      this.headerToggle();
    },
    headerToggle: function() {
      let topY = window.pageYOffset;
      this.fixed = topY > 50;
    }
  },
  mounted() {
    let cached = null;
    let self = this;
    this.headerToggle();

    window.addEventListener("scroll", function(event) {
      if (!cached) {
        setTimeout(() => {
          self.headerToggle();
          cached = null;
        }, 120);
      }
      cached = event;
    });
  }
};
</script>

<style lang="scss">
