.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s ease;

  &.mobileMenu {
    nav a {
      &:nth-of-type(odd) {
        left: -10px;
      }

      &:nth-of-type(even) {
        left: 10px;
      }
    }
  }
}

.fade-enter,
.fade-leave-active {
  opacity: 0;

  &.mobileMenu {
    .imageNamelogo {
      opacity: 0;
    }

    nav a {
      opacity: 0;

      &:nth-of-type(odd) {
        left: 18px;
      }

      &:nth-of-type(even) {
        left: -18px;
      }
    }
  }
}

$burgerWidthStandard: 33px;
$spanDifference: 8px;

.burger {
  width: $burgerWidthStandard;
  height: 19px;
  position: fixed;
  top: 65px;
  right: 39px;
  z-index: 99;
  cursor: pointer;
  transition: all 0.3s ease;

  span {
    width: $burgerWidthStandard;
    height: 3px;
    background-color: $color-light;
    display: block;
    position: absolute;
    transform: translate(-50%, -50%);
    transition-property: transform, width, background-color;
    transition-duration: 0.3s, 0.3s, 0.3s;
    transition-timing-function: ease, ease, ease;
    left: 50%;
    transform-origin: 0% 0%;

    &:nth-of-type(1) {
      top: calc(50% + #{$spanDifference});
    }

    &:nth-of-type(2) {
      top: 50%;
    }

    &:nth-of-type(3) {
      top: calc(50% - #{$spanDifference});
    }
  }

  &.active {
    span {
      background-color: $color-brown;

      &:nth-of-type(1),
      &:nth-of-type(2),
      &:nth-of-type(3) {
        top: 50%;
      }

      &:nth-of-type(2) {
        width: 0%;
      }

      &:nth-of-type(1),
      &:nth-of-type(3) {
        transition-delay: 0.3s, 0s, 0s;
      }

      &:nth-of-type(1) {
        transform: rotate(-45deg) translate(-50%, -50%);
      }

      &:nth-of-type(3) {
        transform: rotate(45deg) translate(-50%, -50%);
      }
    }
  }

  &.fixed:not(.active) {
    background-color: $color-light;
    top: 10px;
    right: 10px;
    width: calc(#{$burgerWidthStandard} + 10px);
    height: 39px;

    span {
      background-color: $color-brown;
      width: calc(#{$burgerWidthStandard} - 10px);
    }
  }
}

.mobileMenu {
